import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classNames from "classnames";

export class Alert extends Component {
    static propTypes = {
        id: PropTypes.number.isRequired,
        type: PropTypes.string,
        title: PropTypes.string,
        message: PropTypes.string.isRequired,
        onDismiss: PropTypes.func,
    };

    constructor(props) {
        super(props);
        this.onDismiss = this.onDismiss.bind(this);
    }
    
    onDismiss() {
        const {id} = this.props;
        console.log('onDismiss', this.props, id);
        this.props.onDismiss(id);
    }

    renderDismisser() {
        return (
            <button type="button" className="close" aria-label="Close"
                    onClick={this.onDismiss}>
                <span aria-hidden="true">&times;</span>
            </button>
        );
    }

    render() {
        const {key, title, message, type, onDismiss} = this.props;
        const dismissible = typeof onDismiss === 'function';
        const className = classNames('alert my-3', {
            'alert-dismissible': dismissible,
            'alert-primary': type === 'primary',
            'alert-success': type === 'success',
            'alert-info': type === 'info' || type === undefined,
            'alert-secondary': type === 'secondary',
            'alert-danger': type === 'danger',
            'alert-warning': type === 'warning',
            'alert-light': type === 'light',
            'alert-dark': type === 'dark',
        });
        return (
            <div className={className} key={key}>
                <strong className="mr-1">{title || ''}</strong>
                {message}
                {dismissible ? this.renderDismisser() : null}
            </div>
        )
    }
}
export default class AlertList extends Component {
    static propTypes = {
        alerts: PropTypes.arrayOf(PropTypes.shape({
            id: PropTypes.number.isRequired,
            type: PropTypes.string,
            title: PropTypes.string,
            message: PropTypes.string.isRequired
        })).isRequired,
        onDismiss: PropTypes.func,
    };

    renderAlerts() {
        const {alerts, onDismiss} = this.props;
        const dismissible = typeof onDismiss === 'function';

        return alerts.map((alert) => {
            return (
                <Alert {...alert} key={alert.id} onDismiss={onDismiss}/>
            )
        })
    }

    render() {
        return (
            <div>
                {this.renderAlerts()}
            </div>
        )
    }
}
