import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import {TAB_SKUS, TAB_BY_COLOR, TAB_COLORS, TAB_MIXES, TAB_GROUPS, TAB_CATEGORIES} from "./constants";
import AlertList from './AlertList';
import {dismissAlert, loadPermissions} from "./actions/app";
import {version} from "../package";
import {connect} from "react-redux";
import {NavLink} from 'react-router-dom';
import {fetchGroups} from "./actions/groups";


class Navigation extends Component {
    static propTypes = {
        alerts: PropTypes.array,
    };

    tabs = [TAB_SKUS, TAB_BY_COLOR, TAB_COLORS, TAB_MIXES, TAB_GROUPS, TAB_CATEGORIES];

    constructor(props) {
        super(props);
        this.onDismissAlert = this.onDismissAlert.bind(this);
    }

    componentDidMount() {
        this.props.dispatch(loadPermissions());
        this.props.dispatch(fetchGroups());
    }


    onDismissAlert(id) {
        this.props.dispatch(dismissAlert(id));
    }

    renderTabList() {
        const tablist = this.tabs.map(t => {
            return (
                <li key={t.key} role="presentation" className="nav-item">
                    <NavLink to={t.key} className="nav-link">{t.title}</NavLink>
                </li>
            )
        });
        return (
            <ul className="nav nav-tabs">
                {tablist}
            </ul>
        )
    }

    render() {
        return (
            <div>
                {this.renderTabList()}
                <AlertList alerts={this.props.alerts} onDismiss={this.onDismissAlert}/>
            </div>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    const { alerts, currentTab } = state.app;
    return { alerts, currentTab };
};

export default connect(mapStateToProps)(Navigation);

