import React, { Component } from 'react';
import { ellipses } from './utils';
import GTIN from './GTIN';
import UPCByColorEditor from './UPCByColorEditor';
import DataTable, {DataTableFilter} from './chums-components/DataTable';
import PropTypes from "prop-types";
import {connect} from "react-redux";
import {fetchColorUPCList, saveColorUPC, selectColorUPC, updateColorUPC} from "./actions/colorUPC";
import {RowsPerPageSelect} from "./common";

class UPCByColorTab extends Component {
    static propTypes = {
        list: PropTypes.array.isRequired,
        selected: PropTypes.object,
        loading: PropTypes.bool,
        isAdmin: PropTypes.bool,
    };

    state = {
        filter: '',
        showInactive: false,
        rowsPerPage: 10,
        currentPage: 1,
    };

    constructor(props) {
        super(props);
        this.onChangeFilter = this.onChangeFilter.bind(this);
        this.onClickNew = this.onClickNew.bind(this);
        this.onChangeRows = this.onChangeRows.bind(this);
        this.onSelect = this.onSelect.bind(this);
        this.onToggleInactive = this.onToggleInactive.bind(this);
        this.onReload = this.onReload.bind(this);
        this.onChange = this.onChange.bind(this);
        this.onSave = this.onSave.bind(this);
    }

    componentDidMount() {
        if (this.props.list.length === 0) {
            this.onReload();
        }
    }

    onReload() {
        this.props.dispatch(fetchColorUPCList());
    }


    onToggleInactive() {
        this.setState({
            showInactive: !this.state.showInactive
        });
    }

    onChangeRows(ev) {
        this.setState({
            rowsPerPage: Number(ev.target.value) || 10
        })
    }

    onChangeFilter(val) {
        this.setState({
            filter: val,
        });
    }

    onSelect(upc) {
        this.props.dispatch(selectColorUPC(upc));
    }

    onClickNew() {
        this.props.dispatch(selectColorUPC());
    }

    onChange(field, value) {
        this.props.dispatch(updateColorUPC(field, value));
    }

    onSave(upc) {
        this.props.dispatch(saveColorUPC(upc));
    }

    renderNotes(text) {
        return (
            <span title={text}>{ellipses(text, 25)}</span>
        )
    }

    renderCompany(val) {
        switch (val) {
        case 'chums':
            return 'CHI';
        case 'bc':
            return 'BCS';
        }
    }

    renderUPCByColorCol(text, row) {
        if (row === undefined) {
            console.log('renderUPCByColorCol()', text, row);
        }
        return row.company === null ? 'N/A' : (row.UDF_UPC_BY_COLOR === row.upc ? 'Y' : 'N');
    }

    render() {
        const {filter, rowsPerPage, showInactive} = this.state;
        const {list, isAdmin, loading, selected} = this.props;
        const notes = '';
        let reFilter;
        let upcFilter;
        try {
            reFilter = new RegExp('\\b' + filter, 'i');
            upcFilter = new RegExp(filter, 'i');
        } catch (err) {
            reFilter = /./i;
            upcFilter = /./i;
        }
        const rows = list
            .filter(item => showInactive || item.active)
            .filter(item => {
                return item.id === 0
                    || filter.trim() === ''
                    || reFilter.test(item.ItemCode)
                    || upcFilter.test(item.upc)
                    || reFilter.test(item.notes || '')
            });

        const tableFields = {
            company: {title: 'Co', render: this.renderCompany},
            ItemCode: 'Item Code',
            ItemCodeDesc: 'Desc',
            ProductType: 'Type',
            upc: {title: 'UPC', render: (text) => GTIN.formatUPC(text), className: 'upc'},
            notes: {title: 'Notes', render: this.renderNotes},
            UDF_UPC_BY_COLOR: {title: 'match', render: this.renderUPCByColorCol, sort: false}
        };

        return (
            <div>
                <DataTableFilter filter={filter} showInactive={showInactive}
                                 onReload={this.onReload}
                                 onChangeFilter={this.onChangeFilter}
                                 onToggleInactive={this.onToggleInactive}>
                    <div className="form-group">
                        <label>Rows per page</label>
                        <RowsPerPageSelect rowsPerPage={rowsPerPage} onChangeRows={this.onChangeRows}/>
                    </div>
                    <button type="btn" className="btn btn-sm btn-outline-secondary order-3" onClick={this.onClickNew} disabled={1 === 0}>
                        New Color UPC
                    </button>
                    {notes}
                </DataTableFilter>
                <div className="row">
                    <div className="col-sm-8">
                        <DataTable fields={tableFields} data={rows} defaultSort="ItemCode"
                                   onSelect={this.onSelect} selected={selected}
                                   loading={loading} pageMaxButtons={9}
                                   rowsPerPage={rowsPerPage}/>
                    </div>
                    <div className="col-sm-4">
                        <UPCByColorEditor item={selected} isAdmin={isAdmin} onChange={this.onChange} onSave={this.onSave}/>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    const { list, selected, loading } = state.colorUPC;
    const {isAdmin} = state.app;
    return { list, selected, loading, isAdmin };
};

export default connect(mapStateToProps)(UPCByColorTab);
